<template>
<div class="pingcang" v-show="showPing" >
  <transition name="saveP">
  <div class="content" v-show="showPing">
    <div class="news">{{$t('OrderInform')}}</div>
    <div class="con con1">
      <span>{{$t('Name')}}:</span><span>{{lastList.nameC}}</span>
    </div>
     <div class="con con2">
      <span>{{$t('code')}}:</span><span>{{lastList.nameE}}</span>
    </div>
     <div class="con con3">
      <span>{{$t('direction')}}:</span><span :class="[lastList.text===$t('sell')?'changeGre':'changeRed']">{{lastList.text}}</span>
    </div>
     <div class="con con4">
      <span>{{$t('PL')}}($):</span><span :class="[lastList.money>0?'changeRed':(lastList.money<0?'changeGre':'')]">{{lastList.money}}</span>
    </div>
     <div class="con con5">
      <span>{{$t('LatestPrice')}}:</span><span :class="[closeprice>0?'changeRed':(closeprice<0?'changeGre':'')]">{{newPrice}}</span>
    </div>
     <div class="con">
      <span>{{$t('AveragePrice')}}:</span><span>{{lastList.Jprice}}</span>
    </div>
     <div class="con">
      <span>{{$t('Qty')}}:</span><span>{{lastList.num}}</span>
    </div>
     <div class="con">
      <span>{{$t('margin')}}:</span><span>{{marginCas}}</span>
    </div>
     <div class="con">
      <span>{{$t('ServiceCharge')}}:</span><span>{{sellHand}}</span>
    </div>
    <div class="newFooter">
      <div class="nf-right" @click="Back"><span>{{$t('cancel')}}</span></div>
      <div class="nf-left" @click="ConfirmClosing" ><span>{{$t('ConfirmC')}}</span></div>
    </div>
  </div>
  </transition>
</div>
</template>
<script>
  import { Toast } from 'vant';
  import EP from '../../utils/encryption'
export default {
  data(){
    return {
      selltype:this.$t('buy'),
      orderList:[],
      stepperMax:0,
      instrumentid:'',
      uid:'',
      detailedData:[],
      stepper:0,
      positionid:[],
      tsNum:0,
      sellHand:0,
      holdPositions3:[]
    }
  },
  props:{
    showPing:{

    },
    holdPositions2:{

    },
    marginCas:{
      type:Number
    },
    tradeType:{
      type:String,
      default: '123.12'
    },
    showBsell:{
      type:Boolean,
    },
    tickName:{
      type:String
    },
    value1:{
      // type:Number
    },
    value2:{
      // type:Number
    },
    priceType:{
      type:String
    },
    ticknameT2:{
      type:String
    },
    HandCash:{
      // type:Number
    },
    mCash:{
      // type:Number
    },
    newPrice:{

    },
    closeprice:{
      type:Number
    },
    lastList:{
      type:Object
    },
    sellBuy2:{

    },
    Value:{

    }
  },
  created(){
    this.selltype = this.$route.params.type;
    console.log(this.selltype,'this.selltype')
  },
  mounted(){
    this.stepperMax=this.lastList.vol;
    this.instrumentid=this.sellBuy2.instrumentid;
    console.log(this.holdPositions2)
    this.uid=this.sellBuy2.uid;
    this.stepper=this.lastList.num;
    console.log( this.stepper,'this.stepper')
    let dis = EP.decrypt(localStorage.getItem('discount'),'')
    let discount = JSON.parse(dis);
    for(let obj in discount.data){
      if(obj===this.holdPositions3.groupid){
        console.log(this.stepper)
        this.sellHand=(this.holdPositions3.commissionClose*discount.data[obj]/100)*this.stepper;
      }
    }
    this.loadposition();
  },
  methods:{
    ConfirmClosing(){
      let _this = this
      this.global.ws.onopen = this.onopen3()
      this.global.ws.onmessage = function(res) {
        _this.websocketonmessage3 (res)
      };
    },
    onopen3 () { // websocket连接后发送数据(send发送)
      console.log(this.stepper,'this.stepper')
      this.setOrderList(this.stepper)
      let balance,direction,actions;
      let that = this
      let diff = that.stepper
      let routeData = this.$route.params
      if(this.directionClose === this.$t('buy')){
        direction = 0
      }else if(this.directionClose === this.$t('sell')){
        direction = 1
      }
      console.log(this.orderList,'this.orderList')
      if(this.orderList.length<=1){
        balance = {
          action: "placeorder",
          reqID: 34,
          data: {
            id: null,
            userid: that.uid,
            settlementid: null,
            source: null,
            type: 1,
            positionid: that.orderList[0].id,
            instrumentid: that.instrumentid,
            orderRef: "9f538075-fff6-4e84-9fee-505ce91fecf6",
            ordersysid: null,
            direction: direction,
            volume: that.stepper,
            volumeTraded: null,
            priceType: 1,
            limitPrice: 0,
            distanceStopPrice: null,
            distanceProfitPrice: null,
            status: null,
            closed: null,
            frozenMargin: null,
            frozenMarginMember: null,
            tradingDay: null,
            createtime: null,
            updatetime: null,
            canceltime: null,
            hedge: null,
            hedgeChannel: null,
            distanceStop: 0.0,
            distanceProfit: 0.0
          }
        }
        console.log(balance,'balance')
        that.global.ws.send(JSON.stringify(balance))
      }else {
        let sum = 0
        for (let i=0;i<this.orderList.length;i++){
          balance = {
            action: "placeorder",
            reqID: 34,
            data: {
              id: null,
              userid: that.uid,
              settlementid: null,
              source: null,
              type: 1,
              positionid: that.orderList[i].id,
              instrumentid: that.instrumentid,
              orderRef: "9f538075-fff6-4e84-9fee-505ce91fecf6",
              ordersysid: null,
              direction: direction,
              volume: that.orderList[i].volume,
              volumeTraded: null,
              priceType: 1,
              limitPrice: 0,
              distanceStopPrice: null,
              distanceProfitPrice: null,
              status: null,
              closed: null,
              frozenMargin: null,
              frozenMarginMember: null,
              tradingDay: null,
              createtime: null,
              updatetime: null,
              canceltime: null,
              hedge: null,
              hedgeChannel: null,
              distanceStop: 0.0,
              distanceProfit: 0.0
            }
          }
          if(i<this.orderList.length-1){
            sum = sum + this.orderList[i].volume
          }
          if(i === this.orderList.length-1){
            balance = {
              action: "placeorder",
              reqID: 34,
              data: {
                id: null,
                userid: this.uid,
                settlementid: null,
                source: null,
                type: 1,
                positionid: that.orderList[i].id,
                instrumentid: this.instrumentid,
                orderRef: "9f538075-fff6-4e84-9fee-505ce91fecf6",
                ordersysid: null,
                direction: direction,
                volume: diff-sum,
                volumeTraded: null,
                priceType: 1,
                limitPrice: 0,
                distanceStopPrice: null,
                distanceProfitPrice: null,
                status: null,
                closed: null,
                frozenMargin: null,
                frozenMarginMember: null,
                tradingDay: null,
                createtime: null,
                updatetime: null,
                canceltime: null,
                hedge: null,
                hedgeChannel: null,
                distanceStop: 0.0,
                distanceProfit: 0.0
              }
            }
          }
          that.global.ws.send(JSON.stringify(balance))
        }
      }
      Toast.loading({
        message: that.$t('quickC'),
        duration: 0,
        mask:true,
        forbidClick: true
      });
      console.log(JSON.stringify(balance))
    },
    websocketonmessage3(e){
      let that = this
      this.redata = JSON.parse(e.data)
      console.log(this.redata,'1111')
      if(this.redata.action === 'placeorder'){
        if(this.redata.data.code === 0){

        }else {
          console.log(that.redata.data.msg)
          Toast.fail({
            message: that.redata.data.msg,
            duration: 1000
          });
        }
      }
      if(this.redata.action === 'notifitrade' ||this.redata.action === 'notifiorder'){
        this.tsNum++
        console.log(that.tsNum >= that.orderList.length)
        if(that.tsNum >= that.orderList.length){
          Toast.clear();
          that.$emit('refresh')
          that.$router.go(-1);
        }
      }
    },
    setOrderList(a){
      let sarr = a
      let idx = 0
      let arr = this.detailedData
      console.log(arr,'a11111')
      console.log(this.detailedData.length)
      this.factorial(arr,sarr,idx)
      console.log(2121212)
    },
    factorial(arr,sarr,idx){
      this.orderList.push(arr[idx])
      if(arr[idx].volume >= sarr){
        return arr[idx].volume
      }else {
        return this.factorial(arr,sarr-arr[idx].volume,idx+1)
      }
    },
    // 获取初始持仓单
    loadposition(msg) {
      let that = this;
      if (that.global.ws && that.global.ws.readyState === 1) {
        that.global.ws.onopen = that.setLoadposition()
      }
      that.global.ws.onmessage = function(res) {
        that.getLoadposition(res)
      };
    },
    setLoadposition() { // websocket连接后发送数据(send发送)
      let actions;
      let that = this
      actions = {
        action: "loadposition",
        reqID: 45,
        data: {
          userId : that.uid,
        },
      }
      that.global.ws.send(JSON.stringify(actions))
    },

    getLoadposition (e) { // 数据接收
      this.redata = JSON.parse(e.data)
      console.log(this.redata)
      if(this.redata.action === "notifitrade" && this.redata.data != null){
      }
      if(this.redata.action === "loadposition" && this.redata.data.code === 0 && this.redata.data.object != null){
        console.log(this.redata.data.object.length)
        for(let i=0;i<this.redata.data.object.length;i++){
          console.log(this.instrumentid === this.redata.data.object[i].instrumentid)
          if(this.instrumentid === this.redata.data.object[i].instrumentid){
            console.log(this.redata.data.object[i])
            this.redata.data.object[i].volume = this.redata.data.object[i].volume - this.redata.data.object[i].volumeClosed -this.redata.data.object[i].volumeFrozen
            this.detailedData.push(this.redata.data.object[i])
          }
        }
        console.log(this.detailedData)
      }

    },
    Back(){
      this.$emit('hidePing',false)
    }
  },
  watch:{
    Value(){
      this.stepper=this.Value;
    },
    holdPositions2(newVAl){
      console.log(newVAl,'this.positionid')
      this.holdPositions3=newVAl;
      this.positionid=this.holdPositions3.id
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "../../style/theme2";
  .pingcang {
    position: absolute;
    z-index: 3;
    left: 0;
    bottom: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,.2);
    .content {
      width: tovw(300);
      height: tovw(400);
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(tovw(-150),tovw(-195),0);
      background-color: #FFF;
      border-radius: tovw(8);
      .news {
        height: tovw(50);
        line-height: tovw(50);
        text-align: center;
        border-bottom: tovw(1) solid #f4f4f4;
      }
      .con {
        width: 100%;
        padding: 0 tovw(20);
        height: tovw(34);
        font-size: tovw(14);
        display: flex;
        &>span:nth-child(1){
            display: inline-block;
            line-height: tovw(34);
            text-align: left;
            flex: 1;
        }
        &>span:nth-child(2){
            display: inline-block;
            line-height: tovw(34);
            text-align: right;
            flex: 1;
        }
      }
      .con3,.con4,.con5 {
        &>span {
          &.changeGre {
              color:#25B100;
            }
          &.changeRed {
              color:#FE0000;
            }
        }
      }
      .newFooter {
        height: tovw(42);
        padding:   0;
        line-height: tovw(42);
        text-align: center;
        display: flex;
        .nf-left{
        position: relative;
          &>span {
            display: block;
            width: 70%;
            height: tovw(30);
            position: absolute;
            left: 50%;
            top:50%;
            transform: translate3d(-50%,-50%,0);
            background-color: #2E8CFF;
            line-height: tovw(30);
            border-radius: tovw(15);
            color: #FFF;
          }
        }
        .nf-right{
          position: relative;
           &>span {
            display: block;
            width: 70%;
            height: tovw(30);
            position: absolute;
            left: 50%;
            top:50%;
            transform: translate3d(-50%,-50%,0);
            background-color: #EBEBEB;
            line-height: tovw(30);
            border-radius: tovw(15);
            color: #222222;
          }
        }
        div {
          flex: 1;
        }
      }

    }
  }
  .saveP-enter{
    opacity: 0;
  }
  .savePl-enter-to{
    opacity: 1;
  }
  .saveP-leave-to{
    opacity: 0;
  }
  .saveP-enter-active{
    transition:all 0.4s ease
  }
  .saveP-leave-active{
    transition:all 0.3s ease
  }
</style>



